import { React } from "react";
import "../App.css";

const ChannelButton = (props) => {
  const { id, name, changeChannel } = props;
  return (
    <div>
      {name}
      <button
        onClick={() => {
          changeChannel(id);
        }}
      >
        Get in
      </button>
    </div>
  );
};

export default ChannelButton;
